.large-hero {
    border-bottom: 10px solid $mainBlue;
	position: relative;

    &__image{
        display: block;
    }
    
	&__text-content {
		left: 0;
		position: absolute;
		text-align: center;
		top: 50%;
		transform: translate(0, -50%);
		width: 100%;
	}

	&__title {
		color: $mainBlue;
		font-size: 2.4rem;
		font-weight: 300;
		margin: 0;

		@mixin atSmall {
			font-size: 4.8rem;
		}

	}

	&__subtitle  {
		color: $mainBlue;
		font-size: 1.5rem;
		font-weight: 300;
		margin: 0;

		@mixin atSmall {
			font-size: 2.9rem;
		}
	}

	&__description {
		color: #fff;
		font-size: 1.875rem;
		font-weight: 100;
		text-shadow: 2px 2px 0 rgba(0, 0, 0, .1);
		max-width: 30rem;
		margin-left: auto;
		margin-right: auto;

		@mixin atSmall {
			font-size: 1.875rem;
		}
		
	}

}
