.btn {
	background: $mainBlue;
	color: #fff;
	display: inline-block;
	padding: .75rem 1.2rem;
	text-decoration: none;

	&--orange {
		background: $mainOrange;
	}

	&--large {
		padding: 1.1rem 1.9rem;
        
        @mixin atSmall {
            font-size: 1.25rem;
        }
        
	}
}